import Vue from 'vue'
import './plugins/vuetify'
import 'vuetify/dist/vuetify.min.css' // Ensure you are using css-loader
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'
import firebase from 'firebase/app'
import 'firebase/auth'
import AlertCmp from './components/Shared/Alert.vue'

Vue.config.productionTip = false
Vue.component('app-alert', AlertCmp)

new Vue({
  router,
  store,
  render: h => h(App),
  created () {
	firebase.initializeApp({
		apiKey: "AIzaSyCjHa8lFDobxK2HvZU0xrbLyLbPDchJ7q8",
		authDomain: "tesztsorozat-c5fc8.firebaseapp.com",
		databaseURL: "https://tesztsorozat-c5fc8.firebaseio.com",
		projectId: "tesztsorozat-c5fc8",
		storageBucket: "tesztsorozat-c5fc8.appspot.com",
		messagingSenderId: "537112287499",
		appId: "1:537112287499:web:7f94f180b8c3f6a9"

    })
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.$store.dispatch('autoSignIn', user)
      }
    })

	axios.interceptors.request.use(async config => {
		let masik = 'Bearer '
		let kapotttoken = await firebase.auth().currentUser.getIdToken(true)
		config.headers.token = masik.concat(kapotttoken)
		return config
	}, (error) => {
		return Promise.reject(error)
	})
	
  }
}).$mount('#app')